.card {
    /* background: #fff; */
    border-radius: 2px;
    height: 190px;
    margin: .5rem;
    position: relative;
    width: 300px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    background: white;
  }
  .card > .img-container > img {
    width: 100%
  }
  
